@import "../../../styles.scss";
.task,
.taskInput {
  padding: 0px 5px;
  height: 47px;
  width: 100%;
  //
  display: flex;
  justify-content: space-between;
  align-items: center;

  // color: white;
  font-size: 12px;
  font-weight: 300;
  @media (min-width: 1200px) {
    font-size: 15px;
    height: 60px;
  }
}
.task:not(:last-of-type) {
  border-bottom: 0.7px solid rgb(74, 74, 74);
}
.taskInput {
  border-radius: 5px;
  background-color: hsl(235, 24%, 19%);
  box-shadow: 0px 0px 20px 2px rgba(13, 18, 48, 0.6);
  input {
    padding: 0px 20px;
    padding-top: 4px;
    background-color: hsl(235, 24%, 19%);
    color: white;
    border: 0px;
    align-self: center;
    height: 20px;
    margin: auto;
  }
}
.lightTaskInput {
  background-color: #fff;
  input {
    color: hsl(235, 24%, 19%);
    background-color: white;
  }
}
.text {
  flex: 0 1 80%;
}

input.text {
  background-color: rgba(white, 0);
  border: 0px;
  border-bottom: solid 1px $brightBlue;
  color: white;
  height: auto;
  padding: 7px 0px;
  font-size: 12px;
  border-radius: 0px;
}
.cross,
.edit {
  flex: 0 1 7%;
  padding: 10px 5px;
  background-color: rgba(white, 0);
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mark {
  flex: 0 1 10%;
  padding: 10px 10px 10px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
