.DT {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  max-width: 500px;
  min-width: 330px;
  font-size: 12px;
  font-weight: 400;
}

select {
  font-size: 11px;
  color: white;
  background-color: rgba(#fff, 0.01);
  border: 0;
  outline: 0;
}
option {
  color: black;
}
.about {
  cursor: pointer;
  color: white;
}

.light .dark {
  select {
    font-size: 11px;
    color: rgb(26, 44, 142);
  }
  color: rgb(32, 48, 138);
  .about {
    cursor: pointer;
    color: rgb(32, 48, 138);
  }
}
